
.form {
  padding: 2% 0;
  min-height:calc(100vh - 97px);
  text-align: center;
  background: linear-gradient(to bottom right, rgb(165, 165, 248), pink);
  &__setting{
    display: inline-block;
    padding: 10px 30px;
    background: none;
    border: 3px solid rgb(112, 153, 199);
    color: rgb(0, 0, 0);
    font-size: 20px;
    font-family: "Open Sans";
    font-weight: 400;
    cursor: pointer; 
    margin-bottom: 20px;
    &:hover {
      color: #453688b7;
      border-color: #7f59bdb7;
    }
  }
  &__date{
    margin-bottom: 10px;
  }
}
.dataset{
  text-align: center;
  margin: auto;
}
.dataset th{
  text-align: center;
  padding: 5px 10px;
  line-height: 20px;
}
.dataset td{
  padding: 3px 0;
}